.App {
  background: #f9f9f9;
}

.avatar {
  padding: 50px 25px 0px;
  text-align: center;
  max-width: 350px;
  margin: 0 auto;
}

.avatar img {
  width: 100%;
  text-align: center;
}

.section-name {
  padding: 10px 0px;
  outline: 0;
  font-size: 2.8em;
  margin: 0 0 0px 0;
  font-weight: bold;
  color:#222;
}

h3.age {
  color:#ccc;
  font-size:.65em;
}

.section-container {
  border: 1px solid #e2e2e2;
  border-radius: 3px;
  background-color: #fff;
  padding: 14px 18px;
  outline: 0;
  margin: 0 0 20px 0;
}

.section-container .row {
  margin-bottom:15px;
}

.section-title {
  color: #ccc;
  font-size: 1.1em;
  margin-bottom: 20px;
  padding: 0px 0px 5px 0px;
  border-bottom: 1px solid #f9f9f9;
}

.contact-info-label {
    text-transform: uppercase;
    font-size:.75em;
    color: #222;
    padding: 2px 0 0 0;
}



