body {
  margin: 0;
  padding: 0;
  font-family: "Lato", "Helvetica Neue", helvetica, sans-serif;
  background-color: #f9f9f9;
  color: #818181;
  -webkit-font-smoothing: antialiased;
}

p {
    margin: 0 0 5px;
}

::-moz-selection {
    background-color: rgba(255,255,255,0.4);
    color: #70a2d3;
    text-shadow: none;
}

::selection {
    background-color: rgba(255,255,255,0.4);
    color: #70a2d3;
    text-shadow: none;
}



